// app/(auth)/signup/page.tsx
'use client';

import Link from 'next/link';
import { useState } from 'react';

export default function Signup() {
  const [passwordStrength, setPasswordStrength] = useState('');

  function checkPasswordStrength(password: string) {
    if (password.length >= 12) return 'Strong';
    if (password.length >= 8) return 'Medium';
    return 'Weak';
  }

  function handlePasswordChange(e: React.ChangeEvent<HTMLInputElement>) {
    const strength = checkPasswordStrength(e.target.value);
    setPasswordStrength(strength);
  }

  return (
    <div className="min-h-screen flex items-center justify-center bg-gray-50 px-6 py-12">
      <div className="w-full max-w-md bg-white rounded-2xl shadow-md p-8 space-y-6">
        {/* Logo */}
        <Link href="/" className="text-xl font-bold text-gray-900">
          HUDLE
        </Link>

        {/* Title */}
        <h2 className="text-2xl font-semibold text-gray-800">
          Create your account
        </h2>

        {/* Form */}
        <form className="space-y-4">
          <div>
            <label htmlFor="name" className="block text-sm font-medium text-gray-700">Full Name</label>
            <input
              type="text"
              id="name"
              required
              className="mt-1 w-full rounded-md border border-gray-300 px-3 py-2 text-sm shadow-sm focus:ring-blue-500 focus:border-blue-500"
            />
          </div>

          <div>
            <label htmlFor="email" className="block text-sm font-medium text-gray-700">Work Email</label>
            <input
              type="email"
              id="email"
              required
              className="mt-1 w-full rounded-md border border-gray-300 px-3 py-2 text-sm shadow-sm focus:ring-blue-500 focus:border-blue-500"
            />
          </div>

          <div>
            <label htmlFor="password" className="block text-sm font-medium text-gray-700">Password</label>
            <input
              type="password"
              id="password"
              required
              onChange={handlePasswordChange}
              className="mt-1 w-full rounded-md border border-gray-300 px-3 py-2 text-sm shadow-sm focus:ring-blue-500 focus:border-blue-500"
            />
            {passwordStrength && (
              <p className={`text-xs mt-1 ${passwordStrength === 'Strong' ? 'text-green-600' : passwordStrength === 'Medium' ? 'text-yellow-600' : 'text-red-600'}`}>
                Password strength: {passwordStrength}
              </p>
            )}
          </div>

          <div>
            <label htmlFor="confirmPassword" className="block text-sm font-medium text-gray-700">Confirm Password</label>
            <input
              type="password"
              id="confirmPassword"
              required
              className="mt-1 w-full rounded-md border border-gray-300 px-3 py-2 text-sm shadow-sm focus:ring-blue-500 focus:border-blue-500"
            />
          </div>

          <div className="flex items-center">
            <input
              id="terms"
              type="checkbox"
              required
              className="h-4 w-4 text-blue-600 border-gray-300 rounded"
            />
            <label htmlFor="terms" className="ml-2 text-sm text-gray-600">
              I agree to the <Link href="/terms" className="underline">Terms of Service</Link>
            </label>
          </div>

          <button
            type="submit"
            className="w-full bg-blue-600 text-white py-2 rounded-md text-sm font-medium hover:bg-blue-700 transition"
          >
            Create Account
          </button>
        </form>

        {/* Footer link */}
        <p className="text-sm text-gray-600 text-center">
          Already have an account?{' '}
          <Link href="/login" className="font-medium text-blue-600 hover:underline">Log in</Link>
        </p>
      </div>
    </div>
  );
}
